import uniNavBar from "@/common/uni-nav-bar/uni-nav-bar.vue";
export default {
	data() {
		return {
			sorts:[]
		}
	},
	components:{
		uniNavBar
	},
	methods: {
		getSorts(){
			uni.request({
				url:"https://www.wanandroid.com/tree/json",
				success: (res) => {
					this.sorts=res.data.data
				}
			})
		},
		jumpUrl(item){
			console.log(".....")
			uni.navigateTo({
				url:"../sort/sort_list?cid="+item.id+"&title="+item.name
			})
		}
	},
	onLoad() {
		this.getSorts();
	}
}
